@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

*:focus {
  outline: none;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  color: #2d2f32;
  margin: 0;
}

.container {
  margin: 0 auto;
  width: 1400px;
  max-width: 100%;
  box-sizing: border-box;
  padding: 0 15px;
}

a {
  text-decoration: none;
}

input,
textarea {
  color: #2d2f32;
}

::placeholder {
  font-family: "Roboto", sans-serif;
  color: #a2aab3;
}

#wrapper {
  padding-top: 60px;
  overflow: hidden;
}

.fixed-header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 60px;
  background-color: #47b1c8;
}

.fixed-header .container {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-between;
}

.fixed-header .container .search-form {
  position: relative;
}

.fixed-header .container .search-form input {
  font-size: 0.8125rem;
  font-weight: 400;
  background: #ffffff;
  border: none;
  width: 411px;
  height: 30px;
  border-radius: 15px;
  box-sizing: border-box;
  padding: 0 30px 0 18px;
}

.fixed-header .container .search-form button {
  cursor: pointer;
  display: block;
  width: 30px;
  height: 30px;
  background: url("../img/search.svg") left center no-repeat;
  position: absolute;
  right: 0;
  top: 0;
  border: none;
}

.fixed-header .container .low_see {
  font-size: 1rem;
  color: #ffffff;
  position: relative;
  line-height: 1;
}

.fixed-header .container .low_see::before {
  content: "";
  display: inline-block;
  width: 22px;
  height: 16px;
  background: url("../img/see.svg") center center no-repeat;
  margin-right: 9px;
  margin-bottom: 3px;
  vertical-align: middle;
}

.header {
  padding: 23px 0 29px;
  background: #ffffff;
  background-size: cover;
}

.header .suptext {
  color: #5d636a;
  font-size: 0.8125rem;
}

.header .school-name-row {
  margin-top: 6px;
  display: flex;
  align-items: center;
}

.header .school-name-row .school-logo {
  margin-right: 12px;
  max-width: 40%;
  flex-shrink: 0;
}

.header .school-name-row .school-logo img {
  display: block;
  max-width: 100%;
}

.header .school-name-row .school-name {
  color: #2d2f32;
  font-size: 1.75rem;
  line-height: 2rem;
}

.header .school-contacts-row {
  margin-top: 26px;
  display: flex;
  font-size: 1rem;
}

.header .school-contacts-row .phone {
  padding-left: 19px;
  position: relative;
}

.header .school-contacts-row .phone a {
  color: #474950;
}

.header .school-contacts-row .phone::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 2px;
  width: 15px;
  height: 15px;
  background: url("../img/phone.svg") center center no-repeat;
  background-size: contain;
}